.row {
    border: 1px solid #D9D9D9;
    border-bottom-width: 2px;
    margin-bottom: 10px;
}
.row-header {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: justify;
        justify-content: space-between;
    padding: 10px;
}
.row-header-left {
    -ms-flex: 2 1;
        flex: 2 1;
}
.row-header-right {
    -ms-flex: 1 1;
        flex: 1 1;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center;
    -ms-flex-pack: end;
        justify-content: flex-end;
}

.row-header-title {
    text-transform: uppercase;
    font-size: 0.809em;
}
.row-header-value-1 {
    /*text-align: right;*/
    margin-top: 18px;
}
.row-header-value-2 {
    font-size: 1.618em;
}
.row-header-icon {
    /*padding-right: 5px;*/
}
.row-header-icon > img,
.row-header-icon > svg {
    width: 75px;
    /*https://coolors.co/abe188-f4d35e-ee964b-f95738-f03a47*/
    /*ABE188*/
    /*F4D35E*/
    /*EE964B*/
    /*F95738*/
    /*F03A47*/
}

.row-body {
    border-top: 1px solid #D9D9D9;
    padding: 10px;
    display: none;
}
.row--expanded .row-body {
    display: block;
}
