.app {
    max-width: 400px;
    margin: 0 auto;
    padding: 10px 10px;
}
/* TABLE */
.table {
    padding: 10px;
    margin: 10px auto;
}
.table--small {
    font-size: 14px;
}
.table th, .table td {
    padding: 5px;
}
/* ALIGNMENT */
.text-right {
    text-align: right;
}
.text-center {
    text-align: center;
}
.no-wrap {
    white-space: nowrap;
}



